import React from "react";
import TodoItem from "./TodoItem";
class Item extends React.Component {
  render() {
    const datas = [
      {
        id: 1,
        text: "Jogging",
        completed: false
      },
      {
        id: 2,
        text: "Cycling",
        completed: true
      },
      {
        id: 3,
        text: "Dancing",
        completed: false
      },
      {
        id: 4,
        text: "Studying",
        completed: true
      },
      {
        id: 5,
        text: "Cooking",
        completed: true
      },
      {
        id: 6,
        text: "Eating",
        completed: true
      },
      {
        id: 7,
        text: "Sleeping",
        completed: false
      }
    ];
    return (
      <div className="item">
        {datas.map((datas) => {
          return (
            <TodoItem
              id={datas.id}
              text={datas.text}
              completed={datas.completed}
            />
          );
        })}
      </div>
    );
  }
}
export default Item;
